.inputOption {
    display: flex;
    align-items: center;
    border-radius: 10px;
    color: gray;
    margin-top: 15px;
    cursor: pointer;
    padding: 10px;
}
.inputOption:hover {
    background-color: whitesmoke;
    border-radius: 10px;
}
.inputOption > h4 {
    margin-left: 5px;
}