.header{
    position: sticky;
    top: 0;
    display: flex;
    background-color: white;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgray;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
    z-index: 999;
}
.header__left{
    display: flex;
}
.header__left > img{
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
}
.header__search{
    display: flex;
    padding: 10px;
    align-items: center;
    background-color: #eef3f8; 
    color: gray;
    height: 22px;
    border-radius: 5px;
}
.header__search > input{
    outline: none;
    border: none;
    background: none;
    /* background-color: white; */
    border-radius: 10px;
    padding: 10px;
}
.header__right{
    display: flex;
}

.feed__inputOptions {
    display: flex;
    justify-content: space-evenly;
}